.alerttriangle-icon {
  height: 20px;
  width: 20px;
  position: relative;
  display: none;
  min-height: 20px;
}
.order-quota-pending {
  flex: 1;
  position: relative;
  line-height: 20px;
  font-weight: 600;
}
.h-t-m-l-feature {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.your-order-quota {
  flex: 1;
  position: relative;
  line-height: 20px;
  font-weight: 500;
}
.frame-integrations,
.heading {
  align-self: stretch;
  display: flex;
}
.frame-integrations {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: var(--body-body-md-default-size);
}
.heading {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.icon-wrapper-for-auto-layout {
  height: 16px;
  width: 16px;
  position: relative;
  display: none;
  min-height: 16px;
}
.label {
  align-self: stretch;
  position: relative;
  line-height: 16px;
  font-weight: 500;
}
.icon-wrapper-for-auto-layout1 {
  height: 16px;
  width: 16px;
  position: relative;
  display: none;
  min-height: 16px;
}
.button-2 {
  align-self: stretch;
  border-radius: var(--border-border-radius-button);
  background-color: var(--color-bg-surface-surface);
  box-shadow: var(--shadow-button-default);
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--space-150) var(--space-300);
  gap: var(--space-050);
}
.icon-wrapper-for-auto-layout2 {
  height: 16px;
  width: 16px;
  position: relative;
  display: none;
  z-index: 1;
}
.label1 {
  width: 100%;
  border: 0;
  outline: 0;
  font-weight: 500;
  font-family: var(--body-body-sm-medium);
  font-size: var(--body-body-sm-medium-size);
  background-color: transparent;
  height: 16px;
  flex: 1;
  position: relative;
  line-height: 16px;
  color: var(--color-bg-surface-surface);
  text-align: left;
  display: inline-block;
  min-width: 71px;
}
.icon-wrapper-for-auto-layout3 {
  height: 16px;
  width: 16px;
  position: relative;
  display: none;
  z-index: 3;
}
.button-shine,
.shine {
  height: 100%;
  width: 100%;
  position: absolute;
}
.shine {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--border-border-radius-button);
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.07) 88.54%,
    rgba(255, 255, 255, 0.15)
  );
  box-shadow: var(--shadow-button-primary);
}
.button-shine {
  margin: 0 !important;
  top: 28px;
  right: -139px;
  bottom: -28px;
  left: 139px;
  transform: rotate(180deg);
  transform-origin: 0 0;
  z-index: 1;
}
.button-1,
.button-group {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.button-1 {
  width: 139px;
  border-radius: var(--border-border-radius-button);
  background-color: var(--color-bg-fill-fill-brand);
  padding: var(--space-150) var(--space-300) var(--space-150) var(--space-300);
  box-sizing: border-box;
  position: relative;
}
.button-group {
  height: 28px;
  gap: var(--space-button-group-gap);
  font-size: var(--body-body-sm-medium-size);
  color: var(--color-bg-fill-fill-brand);
}
.card,
.order-quota {
  flex: 1;
  display: flex;
  align-items: center;
  max-width: 100%;
}
.order-quota {
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-base) var(--padding-5xl);
  box-sizing: border-box;
  gap: var(--gap-13xl);
}
.card {
  border-radius: var(--card-radius-figma-only);
  background-color: var(--color-bg-surface-surface);
  box-shadow: var(--shadow-100);
  overflow: hidden;
  flex-direction: row;
  justify-content: center;
  min-width: 249px;
  z-index: 1;
  text-align: left;
  font-size: var(--heading-heading-md-size);
  color: var(--color-text-text-brand);
  font-family: var(--body-body-sm-medium);
}
@media screen and (max-width: 1050px) {
  .label1 {
    flex: 1;
    min-width: 100%;
  }
  .card {
    flex: unset;
    align-self: stretch;
  }
}
@media screen and (max-width: 450px) {
  .order-quota {
    gap: var(--gap-base);
  }
}
